// Importaciones de mdulos necesarios
import Axios from 'axios';
import { useJsApiLoader, GoogleMap, Marker, DirectionsRenderer } from '@react-google-maps/api';
import { useEffect, useState } from 'react';

// Definicin de constantes y estados iniciales
// Puedes definir origin aqu con un valor por defecto o dejarlo comentado y asignarlo ms adelante
// const origin = { lat: 0, lng: 0 };

export default function Mapa() {
  // Carga de la API de Google Maps
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
  });

  // Estados para las respuestas de direcciones, distancia, duracin y destino seleccionado
  // Respuesta de direcciones
  const [directionsResponse, setDirectionsResponse] = useState('');
  // Distancia de la ruta
  const [distance, setDistance] = useState('');
  // Duracin de la ruta
  const [duration, setDuration] = useState('');
  // Destino seleccionado
  const [selectedDestination, setSelectedDestination] = useState(1);

  // Estado para almacenar los datos de destino obtenidos de Axios
  const [destinationData, setDestinationData] = useState([]);

  // Obtencion de ubicacion actual
  const [origin, setOrigin] = useState({ lat: 0, lng: 0 });



  // Solicitar datos de destino a la API de MediMinder en el backend
  useEffect(() => {
    // Peticin POST a la API de MediMinder en el backend
    Axios.post('https://mediminder-e6ow.onrender.com/api/ips/filtro/')
      .then(response => {
        // Extraccin de destinos vlidos de la respuesta de Axios
        const ipsValidas = response.data.ips_validas;

        // Mapeo de destinos vlidos al formato deseado
        const destinations = ipsValidas.map((ip, index) => ({
          lat: Number(ip.latitud),
          lng: Number(ip.longitud),
        }));

        // Almacenamiento de los destinos en el estado
        console.log(destinations);
        setDestinationData(destinations);
      })
      .catch(error => {
        // Manejo de errores
        console.error('Error al procesar los datos', error);
      });

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        console.log(position.coords.latitude, position.coords.longitude);
        const currentOrigin = { lat: position.coords.latitude, lng: position.coords.longitude };
        // Asigna el valor de origin usando setOrigin
        setOrigin(currentOrigin);
      }, (error) => {
        alert("Error: " + error.code + " " + error.message);
      });
    } else {
      alert("Geolocation is not supported by this browser.");
    }
  }, []);

  // Funcin asincrnica para calcular la ruta desde el origen al destino seleccionado
  async function calculateRoute() {
    // Carga de la API de Google Maps
    const directionsService = new window.google.maps.DirectionsService();
    // Clculo de la ruta
    // El destino se calcula a partir de los datos de ubicacin de los centros de salud
    const results = await directionsService.route({
      // Parmetros de la ruta
      origin: origin,
      destination: destinationData[selectedDestination - 1],
      travelMode: window.google.maps.TravelMode.DRIVING,
    });

    // Actualizacin de estados con los resultados de la ruta
    setDirectionsResponse(results);
    // La distancia de la ruta se obtienen de los resultados de la ruta
    setDistance(results.routes[0].legs[0].distance.text);
    // La duracin de la ruta se obtienen de los resultados de la ruta
    setDuration(results.routes[0].legs[0].duration.text);
  }

  // Se calcula la ruta cada vez que se selecciona un destino
  useEffect(() => {
    calculateRoute();
  }, [selectedDestination, destinationData]);

  // Comprobacin de carga de Google Maps API
  if (!isLoaded) {
    return <div>Loading...</div>;
  }

  // Renderizado del mapa y botones de seleccin de destino
  return (
    <div style={{ height: '700px' }}>
      <GoogleMap center={origin} zoom={15} mapContainerStyle={{ width: '100%', height: '100%' }}>
        <Marker position={origin} />
        <DirectionsRenderer directions={directionsResponse} />
      </GoogleMap>

      {/* Renderizacin de botones basados en los datos recibidos de Axios */}
      {destinationData.map((destination, index) => (
        <button key={index} onClick={() => setSelectedDestination(index + 1)}>
          Ruta {index + 1}
        </button>
      ))}
    </div>
  );
}
